.old-tv-screen {
  margin: 0px 0%;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  position: relative;
  border: none;
  border-radius: 50% / 10%;
  text-align: center;
  text-indent: 0.1em;
  text-overflow: clip;
  background: #1b1f1b; }

.old-tv-screen-wrapper {
  width: 90vw;
  height: 100vh;
  margin: 0 5%; }

.old-tv-screen::before {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  position: absolute;
  content: "";
  top: 9.5%;
  right: -5%;
  bottom: 9.5%;
  left: -5%;
  border: none;
  border-radius: 5% / 50%;
  text-overflow: clip;
  background: #1b1f1b;
  text-shadow: none; }
